.parallax-bg {
  background-attachment: fixed;
  background-position: 50% 0;
  background-repeat: repeat-x;
  background-size: cover;
  position: absolute;
  z-index: -1;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
}
.parallax-bg:after {
  background-color: transparent;
  content: "";
  display: block;
  position: absolute;
  z-index: 0;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
}
.page-common-background {
  background-attachment: fixed;
