/* Progressbar*/
.progressbar-container {
  position: relative;
  width: 100%;
  margin-bottom: 20px;
}
.progressbar-container .progressbar-title {
  margin-bottom: 0;
  line-height: 2.2;
}
.progressbar-container .progressbar {
  transition: all 0.4s ease-in-out;
  background-color: #9c5b57;
  width: 0;
}
.progressbar-container .progressbar-back-text,
.progressbar-container .progressbar-front-text {
  display: none;
}
.progressbar-container.basic,
.progressbar-container.style-tooltip {
  position: relative;
}
.progressbar-container.basic .progressbar,
.progressbar-container.style-tooltip .progressbar {
  background-color: #9c5b57;
  height: 20px;
  width: 0;
}
.progressbar-container.basic .progressbar:before,
.progressbar-container.style-tooltip .progressbar:before {
  content: '';
  bottom: 0;
  position: absolute;
  display: block;
  width: 100%;
  height: 20px;
  background-color: #eeeeee;
  z-index: -1;
}
.progressbar-container.basic .progressbar {
  position: static;
}
.progressbar-container.basic .progressbar-back-text {
  position: absolute;
  display: block;
  right: 0;
  font-size: 14px;
  font-weight: 600;
  text-align: right;
}
.progressbar-container.style-tooltip {
  margin-bottom: 40px;
  position: relative;
}
.progressbar-container.style-tooltip .progressbar {
  position: absolute;
}
.progressbar-container.style-tooltip .progressbar-back-text {
  position: absolute;
  display: block;
  width: 100%;
  height: 20px;
  bottom: -20px;
  font-size: 0;
  background-color: #eeeeee;
  z-index: -1;
}
.progressbar-container.style-tooltip .progressbar-front-text {
  position: absolute;
  display: block;
  right: -25px;
  bottom: 28px;
  line-height: 1;
  height: 23px;
  font-weight: 600;
  text-align: center;
  max-width: 50px;
  padding: 5px;
  border-radius: 3px;
  color: #ffffff;
  background-color: #555555;
}
.progressbar-container.style-tooltip .progressbar-front-text:before {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 20px;
  border-left-width: 4px;
  border-right-width: 4px;
  border-top-width: 7px;
  border-style: solid;
  border-color: transparent;
  border-top-color: #555555;
  display: block;
  width: 0;
  height: 0;
}
.progressbar-container.fill-text {
  height: 40px;
  margin-bottom: 35px;
  background-color: #eeeeee;
}
.progressbar-container.fill-text .progressbar-title {
  position: absolute;
  line-height: 40px;
  padding-left: 15px;
  top: 0;
  left: 0;
}
.progressbar-container.fill-text .progressbar {
  position: absolute;
  height: 100%;
  overflow: hidden;
  z-index: 1;
}
.progressbar-container.fill-text .progressbar:before {
  height: 100%;
}
.progressbar-container.fill-text .progressbar .progressbar-title {
  color: #ffffff;
}
.progressbar-container.fill-text .progressbar-back-text,
.progressbar-container.fill-text .progressbar-front-text {
  position: absolute;
  display: block;
  right: 0;
  font-size: 14px;
  line-height: 40px;
  padding-right: 15px;
  font-weight: 600;
  text-align: right;
}
.progressbar-container.fill-text .progressbar-front-text {
  left: 0;
  top: 0;
  color: #ffffff;
}
.progressbar-container.vertical {
  height: 120px;
  float: left;
  margin-right: 50px;
  margin-bottom: 80px;
  background-color: #ffffff;
}
.progressbar-container.vertical.gray {
  background-color: #f4f4f4;
}
.progressbar-container.vertical .progressbar-title {
  display: block;
  position: absolute;
  bottom: -40px;
}
.progressbar-container.vertical .progressbar {
  height: 0;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
}
.progressbar-container.vertical .progressbar-back-text {
  display: block;
  position: absolute;
  max-height: 20px;
  bottom: -50px;
  line-height: 30px !important;
  font-weight: 600;
  color: #9c5b57;
}
